    v-if="this.tracks.length"
    ref="player"
  />
  <!-- <div target="_blank" class="github-btn">
    <input v-model="keyword" class="mr-2" style="color: black" />
    <a @click="debugPushedData(keyword)"
      >Debug: Push track from beatmap set id</a
    >
  </div> -->
</template>

<script>
// eslint-disable-next-line no-unused-vars
import io from 'socket.io-client'
import Player from './components/Player.vue'
import n from 'ant-design-vue/es/notification'
import { Button } from 'ant-design-vue/es'
import EE from 'eventemitter3'
const socketUrl = process.env.NODE_ENV === 'development' ? 'https://bot.ri.mk' : '/'
const historyUrl = process.env.NODE_ENV === 'development' ? 'https://bot.ri.mk/radio/history' : './history'
export default {
  components: {
    Player
  },
  methods: {
    logEvent (e) {
      n.open(e)
    },
    isSameTrack (a, b) {
      return a.sid === b.sid
    },
    trackToggleFavourite (index) {
      this.tracks[index].favorited = !this.tracks[index].favorited
    },
    async debugPushedData (keyword) {
      const beatmapSet = await fetch(`https://api.sayobot.cn/v2/beatmapinfo?K=${keyword}`)
        .then(res => res.json())
        .then(res => res.data)
        .catch(() => undefined)
      if (!beatmapSet) return
      const beatmap = beatmapSet.bid_data.pop()
      const beatmapSetId = beatmapSet.sid
      const cover = `https://dl.sayobot.cn/beatmaps/files/${beatmapSetId}/${beatmap.bg}`
      const source = `https://dl.sayobot.cn/beatmaps/files/${beatmapSetId}/${beatmap.audio}`
      const track = {
        artist: beatmapSet.artistU || beatmapSet.artist,
        name: beatmapSet.titleU || beatmapSet.title,
        cover,
        source
      }
      this.tracks.push(track)
    },
    pushTrack (song) {
      const track = {
        artist: song.artistU || song.artist,
        name: song.titleU || song.title,
        cover: song.background || `https://b.ppy.sh/thumb/${song.sid}l.jpg`,
        sid: song.sid,
        url: song.setLink,
        source: song.fullMp3 || song.previewMp3,
        uuid: song.uuid,
        uploader: song.uploader
      }
      this.insertIndexOffset += 1
      const insertIndex = this.currentIndex + this.insertIndexOffset
      // if (process.env.NODE_ENV !== 'production') console.log({
      //   insertIndex,
      //   currentIndex: this.currentIndex,
      //   playlistLength: this.tracks.length
      // })
      if (insertIndex >= this.tracks.length) this.tracks.push(track)
      else this.tracks.splice(insertIndex, 0, track)
    },
    socketPushedTrack (song) {
      n.open({
        message: `${song.uploader.nickname} pushed new track!`,
        description: [
          `${song.artistU || song.artist} - ${song.titleU || song.title} (${song.creator})`,
          `uploader qq: ${song.uploader.id}`
        ].join(' \n'),
        placement: 'topRight'
      })
    },
    notificationNeedConfirm (message, description, duration = 5, btn = (n, key) => (<Button size="small" type="primary" onClick={() => n.close(key)}>ok</Button>)) {
      const key = `open${Date.now()}`
      btn = btn(n, key)
      n.open({
        message,
        description,
        key,
        btn,
        duration
      })
    },
    removeTrack ({ uploader, uuid }) {
      const targetIndex = this.tracks.findIndex(({ uuid: _uuid }) => uuid === _uuid)
      const currentIndex = this.currentIndex
      // const target = this.tracks[targetIndex]
      if (targetIndex === -1) {
        return n.open({
          message: `unable to find the song with uuid ${uuid}`,
          placement: 'topRight'
        })
      }
      /** the only track is going to be deleted. ok */
      if (this.tracks.length === 1) return this.removeTrackForReal(targetIndex, `${uploader.nickname} required to remove this track ;^;`)
      /**
       * the track's index is lower than current playing track, remove directly will cause track cover to mismatch the song.
       * need update the player data after the removal
       */
      if (targetIndex === currentIndex) this.$refs.player.nextTrack()
      if (targetIndex <= currentIndex) return this.removeTrackIndexLessThanCurrentIndex(targetIndex, `${uploader.nickname} required to remove this track ;^;`)
      /** the index of track to remove is higher than curret, remove directly */
      this.removeTrackForReal(targetIndex, `${uploader.nickname} required to remove this track ;^;`)
    },
    removeTrackIndexLessThanCurrentIndex (index, reason) {
      /** edge case:
       * 2 track, current index = 1,
       * removing track's index = 1,
       *
       * so nextTrack() will be called, but the next index will be 0 beacuse of the overflow.
       * subtract 1 from 0 will be -1
       */
      if (this.$refs.player.currentTrackIndex > 0) this.$refs.player.currentTrackIndex -= 1
      this.removeTrackForReal(index, reason)
    },
    removeTrackForReal (index, reason = undefined) {
      const removedTrack = this.tracks.splice(index, 1)[0]
      n.open({
        message: `removed ${removedTrack.artist} - ${removedTrack.name}`,
        description: reason,
        placement: 'topRight'
      })
    },
    switchTrack (trackIndex) {
      this.currentIndex = trackIndex
      this.insertIndexOffset = 0
      // if (process.env.NODE_ENV !== 'production') console.log({
      //   currentIndex: trackIndex,
      //   insertIndexOffset: this.insertIndexOffset
      // })
    }
  },
  data () {
    let socket
    try {
      const work = new Worker('workers/webworker-socket.io.js')
      const emitter = new EE()
      const parentApi = {
        emit: (...args) => emitter.emit(...args)
      }
      work.onmessage = function (event) {
        var data = event.data
        if (!data.parentJob) return
        parentApi[data.parentJob](...data.args)
      }
      const workerJob = (action, ...args) => work.postMessage({ action, args })
      workerJob('connect', socketUrl, { path: '/Radio' })
      workerJob('on', 'broadcast-message')
      workerJob('on', 'search-result')
      workerJob('on', 'remove-track')
      socket = emitter
    } catch (error) {
      console.log(error, 'fallback to socketio in main thread')
      socket = io(socketUrl, { path: '/Radio' })
    }
    return {
      keyword: '',
      tracks: [],
      socket,
      currentIndex: 0,
      insertIndexOffset: 0,
      scheduledRemovingTracks: []
    }
  },
  mounted () {
    this.socket.on('search-result', (song) => {
      this.socketPushedTrack(song)
      this.pushTrack(song)
    })
    this.socket.on('broadcast-message', (sender, message) => this.notificationNeedConfirm(message, `${sender.name}`, sender.duration || 30, (n, key) => (<Button size="small" type="primary" onClick={() => n.close(key)}>ok</Button>)))
    this.socket.on('remove-track', (track) => this.removeTrack(track))

    fetch(historyUrl)
      .then(res => res.json())
      .then(songs => songs.map(song => this.pushTrack(song)))
  }

}

</script>

